<template>
  <div class="head">
    <van-nav-bar :title="title" fixed left-text="" @click-left="$router.replace({name: 'Index'})" :left-arrow="showArrow">
      <template #title v-if="$route.name === 'Index'">
        <div class="flex-middle title-box">
          <img src="@/assets/images/logo.png" alt="">
          <div class="index-title">
            <div class="name">ABDX</div>
            <div class="desc">Smart Contract</div>
          </div>
        </div>
      </template>
      <template #right>
        <img class="more" src="@/assets/images/more.png" @click="onMenu" alt="">
      </template>
    </van-nav-bar>
    <!-- 菜单栏 -->
    <van-popup class="menu-Block" position="right" :style="{ width: '42%', height: '100%', top: 'calc(50% + 45px)'}" v-model:show="open">
      <div class="menu-leftBlock">
        <div class="menu-item flex-middle" @click="$router.push({name:'Index'})">
          <img class="menu-img" v-if="$route.name === 'Index'" src="@/assets/images/home_active.png">
          <img class="menu-img" v-else src="@/assets/images/home.png">
          <div class="menu-tit" :class="$route.name === 'Index' ? 'active' : ''">首页</div>
        </div>
        <div class="menu-item flex-middle" @click="$router.push({name:'Share'})">
          <img class="menu-img" v-if="$route.name === 'Share'" src="@/assets/images/share_active.png">
          <img class="menu-img" v-else src="@/assets/images/share.png">
          <div class="menu-tit" :class="$route.name === 'Share' ? 'active' : ''">分享推广</div>
        </div>
        <!-- <div class="menu-item flex-middle" @click="$router.push({name:'RecordInvest'})">
          <img class="menu-img" v-if="$route.name === 'RecordInvest'" src="@/assets/images/invest_active.png">
          <img class="menu-img"  v-else src="@/assets/images/invest.png">
          <div class="menu-tit" :class="$route.name === 'RecordInvest' ? 'active' : ''">投资记录</div>
        </div>
        <div class="menu-item flex-middle" @click="$router.push({name:'RecordSwap'})">
          <img class="menu-img" v-if="$route.name === 'RecordSwap'" src="@/assets/images/swap_active.png">
          <img class="menu-img" v-else src="@/assets/images/swap.png">
          <div class="menu-tit" :class="$route.name === 'RecordSwap' ? 'active' : ''">兑换记录</div>
        </div>
        <div class="menu-item flex-middle" @click="$router.push({name:'RecordBonus'})">
          <img class="menu-img" v-if="$route.name === 'RecordBonus'" src="@/assets/images/reward_active.png">
          <img class="menu-img" v-else src="@/assets/images/reward.png">
          <div class="menu-tit" :class="$route.name === 'RecordBonus' ? 'active' : ''">奖金记录</div>
        </div> -->
        <div class="menu-item flex-middle" @click="$router.push({name:'Team'})">
          <img class="menu-img" v-if="$route.name === 'Team'" src="@/assets/images/team_active.png">
          <img class="menu-img" v-else src="@/assets/images/team.png">
          <div class="menu-tit" :class="$route.name === 'Team' ? 'active' : ''">我的团队</div>
        </div>
        <div class="menu-item flex-middle" @click="$router.push({name:'Leaderboard'})">
          <img class="menu-img" v-if="$route.name === 'Leaderboard'" src="@/assets/images/rank_active.png">
          <img class="menu-img" v-else src="@/assets/images/rank.png">
          <div class="menu-tit" :class="$route.name === 'Leaderboard' ? 'active' : ''">排行榜</div>
        </div>
      </div>
    </van-popup>

  </div>
</template>

<script>
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import web3Data from "@/utils/initWeb3";
import { getLang, setLang } from "@/utils/i18n";
export default {
  props: {
    Headtype: {
      type: String,
      default: ''
    },
    showArrow: {
      type: Boolean,
      default: true
    },
    title: {
      type: String,
      default: ''
    },
  },
  setup (props) {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      open: false,
      Headtype: props.Headtype,
      showArrow: props.showArrow,
      headerActive: 0,
      title: props.title,
      local: getLang().toLowerCase() || "zh-cn",
      changeLanuage: (val) => {
        setLang(val)
        data.local = val
      },
      initContract: () => {
        data.initData()
      },
      initData: () => {
      },
      initWallet: () => {
        web3Data.initWeb3()
      },
      onMenu: () => {
        data.open = !data.open
      },
      openUrl: (url) => {
        window.location.href = url;
      },
      closeMenu: () => {
        data.open = false
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less">
#app {
  .van-overlay {
    top: 46px;
  }
}
</style>
<style lang="less" scoped>
.title-box {
  text-align: left;
  img {
    width: 32px;
    height: 32px;
    margin-right: 5px;
  }
  .name {
    font-size: 16px;
    font-weight: 600;
    line-height: 16px;
    margin-bottom: 3px;
  }
  .desc {
    color: #666;
    font-size: 10px;
    line-height: 10px;
  }
}
.more {
  width: 20px;
  height: 20px;
}
.menu-Block {
  .menu-leftBlock {
    background: #fff;
    padding: 5px 0;
    .menu-item {
      padding: 12px 20px;
      .menu-img {
        width: 20px;
        height: 20px;
      }
      .menu-tit {
        font-size: 15px;
        color: #111;
        margin-left: 15px;
        font-weight: 600;
        &.active{
          color: #2058E9;
        }
      }
    }
  }
}
</style>
